<template>
    <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="approvalConnect1">
            <div>
                <el-form :inline="true" :model="form" style="margin-top: 15px">
                    <el-form-item label="" prop="start_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="" v-model="form.start_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="end_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="" v-model="form.end_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                                v-model="form.apply_id"
                                clearable
                                placeholder=""
                                size="small"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                                v-model="form.operator"
                                clearable
                                placeholder=""
                                size="small"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                                v-model="form.approval_user"
                                clearable
                                placeholder=""
                                size="small"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="approval_type">
                        <el-select v-model="form.approval_type" clearable size="small">
                            <el-option
                                    v-for="item in approvalTypeList"
                                    :key="item.approvalType"
                                    :label="item.approvalTypeName"
                                    :value="item.approvalType"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button
                                type="primary"
                                @click="getApprovalConnectList"
                                size="small"
                                icon="el-icon-search"
                        >
                        </el-button
                        >
                    </el-form-item>
                </el-form>
                <base-table :list="dataList" :data="approvalConnectList">
                    <el-table-column label="" align="center">
                        <template slot-scope="scope">
                            <el-button
                                    type="text"
                                    icon="el-icon-edit-outline"
                                    @click="showApprovalConnect(scope.row.apply_id,scope.row.approval_type,scope.row.relation_id)"
                            >
                            </el-button
                            >
                        </template>
                    </el-table-column>
                </base-table>
                <base-page
                        :page="pageNum"
                        :size="pageSize"
                        :total="total"
                        @size="changeSize"
                        @page="changePage"
                ></base-page>
            </div>
        </el-tab-pane>
        <el-tab-pane label="" name="approvalConnect2">
            <div>
                <el-form :inline="true" :model="form" style="margin-top: 15px">
                    <el-form-item label="" prop="start_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="" v-model="form.start_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="end_date">
                        <el-date-picker type="date" value-format="yyyy-MM-dd" clearable placeholder="" v-model="form.end_date" style="width: 200px;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                                v-model="form.apply_id"
                                clearable
                                placeholder=""
                                size="small"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                                v-model="form.operator"
                                clearable
                                placeholder=""
                                size="small"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                                v-model="form.approval_user"
                                clearable
                                placeholder=""
                                size="small"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="approval_type">
                        <el-select v-model="form.approval_type" clearable size="small">
                            <el-option
                                    v-for="item in approvalTypeList"
                                    :key="item.approvalType"
                                    :label="item.approvalTypeName"
                                    :value="item.approvalType"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button
                                type="primary"
                                @click="getApprovalConnectList"
                                size="small"
                                icon="el-icon-search"
                        >
                        </el-button
                        >
                    </el-form-item>
                </el-form>
                <base-table :list="dataList" :data="approvalConnectList">
                    <el-table-column label="" align="center">
                        <template slot-scope="scope">
                            <el-button
                                    type="text"
                                    icon="el-icon-edit-outline"
                                    @click="showApprovalConnect(scope.row.apply_id,scope.row.approval_type,scope.row.relation_id)"
                            >
                            </el-button
                            >
                        </template>
                    </el-table-column>
                </base-table>
                <base-page
                        :page="pageNum"
                        :size="pageSize"
                        :total="total"
                        @size="changeSize"
                        @page="changePage"
                ></base-page>
            </div>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
    import baseTable from "../../components/Table";
    import basePage from "../../components/Pagination";

    export default {
        components: {
            baseTable,
            basePage,
        },
        name: "approvalConnect",
        data() {
            return {
                activeName: 'approvalConnect1',
                role: sessionStorage.getItem("role"),
                form: {},
                dataList: [
                    {
                        label: "",
                        prop: "apply_id",
                        sort: true,
                    },
                    {
                        label: "",
                        prop: "operator",
                    },
                    {
                        label: "",
                        prop: "name",
                    },
                    {
                        label: "",
                        prop: "approval_user",
                    },
                    {
                        label: "",
                        prop: "approval_user_name",
                    },
                    {
                        label: "",
                        prop: "branch_name",
                    },
                    {
                        label: "",
                        prop: "approval_type_name",
                    },
                    {
                        label: "",
                        prop: "message",
                    },
                    {
                        label: "",
                        prop: "create_time",
                        sort: true,
                    },
                ],
                approvalTypeList:[],
                approvalConnectList: [],
                pageNum: 1,
                pageSize: 10,
                total: 0,
                visible: false,
            };
        },
        mounted() {
            this.getApprovalTypeList();
        },
        watch: {
            visible(val) {
                if (!val) {
                    this.form = {};
                }
            },
        },
        methods: {
            handleClick(tab, event) {
                console.log(tab, event);
                this.form = {};
                this.approvalConnectList=[];
                this.pageNum=1;
                this.pageSize=10;
                this.total=0;
                this.visible=false;
                this.getApprovalConnectList();
            },
            // 
            getApprovalTypeList() {
                if (this.approvalTypeList.length != 0) return;
                this.$api.getApprovalTypeByEnum().then((res) => {
                    this.approvalTypeList = res.data;
                });
            },

            // 
            getApprovalConnectList() {
                if(this.activeName == "approvalConnect1"){//
                    this.$api
                        .getApprovalConnect1List({
                            page: this.pageNum - 1,
                            number: this.pageSize,
                            ...this.form,
                        })
                        .then((res) => {
                            this.approvalConnectList = res.data.approvalConnectList;
                            this.total = res.data.total;
                            //this.form = {};
                        });
                }else{//
                    this.$api
                        .getApprovalConnect2List({
                            page: this.pageNum - 1,
                            number: this.pageSize,
                            ...this.form,
                        })
                        .then((res) => {
                            this.approvalConnectList = res.data.approvalConnectList;
                            this.total = res.data.total;
                            //this.form = {};
                        });
                }
            },

            // 
            showApprovalConnect(apply_id,approval_type,relation_id) {
                this.$router.push({
                    path: "/approvalConnectDetail",
                    name: "",
                    query: {
                        id: relation_id,
                        role: this.role,
                        check: true, // 
                        apply_id: apply_id,
                        approval_type: approval_type,
                        activeName:this.activeName,
                        req_place: '3',
                    },
                    params: {
                        searchKey: this.form,
                    },
                });
            },
            // 
            changeSize(size) {
                this.pageSize = size;
                this.getApprovalConnectList();
            },
            // 
            changePage(page) {
                this.pageNum = page;
                this.getApprovalConnectList();
            },
        },
        // 
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getApprovalConnectList();
            });
        },
    };
</script>



