// 
export function cardFilter(num) {
    if (!num) return '-'
    let reg = /^(\d{4})\d+(\d{4})$/;
    let card = num.replace(reg, "$1******$2");
    return card;
}
// 
export function timeFilter(time) {
    var date =
        time.substring(0, 4) +
        "-" +
        time.substring(4, 6) +
        "-" +
        time.substring(6, 8) +
        " " +
        time.substring(8, 10) +
        ":" +
        time.substring(10, 12) +
        ":" +
        time.substring(12, 14);
    return date;
}
// 
export function cstFilter(flag) {
    const statusMap = {
        "0": "",
        "1": "",
        "3": "",
        "4": "",
        "5": "",
        "6": "",
    }
    return statusMap[flag] || "-";
}


