<template>
  <div>
    <el-button type="primary" size="small" @click="addView()"></el-button>
    <el-button
        type="danger"
        size="small"
        @click="deleteUser"
        :disabled="delList.length == 0"
    >
    </el-button
    >
    <el-button
        type="primary"
        size="small"
        icon="el-icon-folder-add"
        @click="uploadFile"

    >
    </el-button
    >
    <el-button type="warning" size="small" @click="downModal"
    >
    </el-button
    >
    <el-button-group>
      <project></project>
    </el-button-group>
    <el-button type="warning" size="small" @click="exportErrorInfos"></el-button>
    <input type="file" id="file" @change="chooseFile" style="display: none"/>
    <el-form :inline="true" :model="form">
      <el-form-item label="" style="width: 300px">
        <el-select
            v-model="form.responsible"
            clearable
            filterable
            placeholder=""
            size="small"
        >
          <el-option v-for="user in userList" :value="user.account_id" :key="user.account_id" :label="user.name"
          ><span>{{ user.name }}({{ user.account_id }})</span></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" style="width: 300px">
        <el-input v-model="form.id" clearable size="small"></el-input>
      </el-form-item>
      <el-form-item prop="pj_classify" label="" style="width: 300px">
        <el-select
            v-model="form.pj_classify" clearable size="small">
          <el-option
              v-for="item in projectList"
              :label="item.project_name"
              :key="item.pj_classify"
              :value="item.pj_classify">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" style="width: 300px">
        <el-input v-model="form.content_text" filterable clearable size="small">
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="requirement" style="width: 300px">
        <el-input v-model="form.requirement" filterable clearable size="small">
        </el-input>
      </el-form-item>
      <el-form-item
          prop="risk_level"
          label=""
          size="small"
          style="width: 300px">
        <el-select
            v-model="form.risk_level" clearable>
          <el-option
              v-for="item in items2"
              :label="item.label"
              :key="item.label"
              :value="item.label">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="site_no" style="width: 340px">
        <el-cascader v-model="form.site_no" :options="options"
                     filterable clearable
                     :show-all-levels="false" @change="handleChange" size="small">
        </el-cascader>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select readonly size="small" v-model="status" clearable multiple >
          <el-option v-for="item in items"
                     :value="item.value"
                     :key="item.value"
                     :label="item.label">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="rangeDate" style="width: 480px">
        <el-date-picker v-model="dateRange" clearable type="daterange" size="small"
                        range-separator="" start-placeholder="" end-placeholder=""
                        unlink-panels  :picker-options="pickerOptions" value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
            type="primary"
            @click="getMerror(true)"
            size="small"
            icon="el-icon-search"
        >
        </el-button
        >
      </el-form-item>
    </el-form>
    <el-table  style="table-layout: fixed" :data="showData"  @selection-change="chooseData" border :row-style="{height: '80px'}" :cell-style="{padding: '0'}">
      <el-table-column type="selection" width="40">
      </el-table-column>
      <el-table-column label="" prop="index" align="center" type="index" width="50">
      </el-table-column>
      <el-table-column label="" prop="id" align="center">
      </el-table-column>
      <el-table-column label="" prop="site_name" align="center">
      </el-table-column>
<!--      <el-table-column label="" prop="project_name" align="center">-->
<!--      </el-table-column>-->
      <el-table-column label="" prop="content_text" align="center" >
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.content_text" placement="top">
            <div class="showPart" style="height: 70px" >{{scope.row.content_text}}</div>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" prop="risk_level" align="center" width="60">
      </el-table-column>
      <el-table-column label="" prop="end_date" align="center" width="100" sortable>
      </el-table-column>
      <el-table-column label="" prop="requirement" align="center" >
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.requirement" placement="top">
            <div class="showPart" style="height: 70px">{{scope.row.requirement}}</div>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" prop="name" align="center" width="80">
      </el-table-column>
      <el-table-column label="" prop="status" align="center" width="80">
        <template slot-scope="scope" v-if="">
          <span>
            {{ computedStatus(scope.row.status) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="80">
        <template slot-scope="scope">
          <template>
            <el-button
                :disabled="edit(scope.row.status)"
                type="text"
                icon="el-icon-edit-outline"
                @click="editMerror(scope.row)"
            >
            </el-button>
            <el-button
                :disabled="edit(scope.row.status)"
                type="text"
                icon="el-icon-delete"
                style="color: red"
                @click="delOne(scope.row.id)"
            >
            </el-button>
          </template>
        </template>
      </el-table-column>
    </el-table>
    <base-page
        :page="pageNum"
        :size="pageSize"
        :total="total"
        @size="changeSize"
        @page="changePage"
    ></base-page>
    <el-dialog :title="form.id ? '' : ''" :visible.sync="visible" width="45%"
               :before-close="handleClose">
      <el-form ref="form" :model="form" :rules="rules" label-width="90px" :disabled="check">
        <el-form-item
            prop="pj_classify"
            label=""
            style="width: 360px">
          <el-select
              v-model="form.pj_classify" filterable>
            <el-option
                v-for="item in projectList"
                :label="item.project_name"
                :value="item.pj_classify"
                :key="item.pj_classify"
                :disabled="item.project_name ==='' ? true:false">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
            prop="site_no"
            label=""
            style="width: 360px"
        >
          <el-cascader v-model="form.site_no" :options="options"
                       filterable :show-all-levels="false"
                       @change="handleChange" :disabled.sync="disabled">
          </el-cascader>
        </el-form-item>
        <el-form-item
            prop="content_text"
            label=""
            style="width: 360px">
          <el-input type="textarea" size="small"
                    v-model="form.content_text">
          </el-input>
        </el-form-item>
        <el-form-item
            prop="risk_level"
            label=""
            style="width: 360px">
          <el-select
              v-model="form.risk_level"
          >
            <el-option
                v-for="item in items2"
                :label="item.label"
                :value="item.label"
                :key="item.label"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
            prop="end_date"
            label=""
            style="width: 360px">
          <el-date-picker size="small" value-format="yyyy-MM-dd"
                          v-model="form.end_date" type="date"
                          placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item
            prop="requirement"
            label=""
            style="width: 360px">
          <el-input type="textarea" size="small"
                    v-model="form.requirement">
          </el-input>
        </el-form-item>
        <el-form-item
            prop="responsible"
            label=""
            style="width: 360px">
          <el-select v-model="form.responsible" filterable clearable size="small">
            <el-option v-for="item in userList" :key="item.account_id" :label="item.name"
                       :value="item.account_id">
              <span>{{ item.name }}({{ item.account_id }})</span>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose"> </el-button>
        <el-button type="primary" size="small" @click="addOrEditMerror"> 
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import BranchTreePicker from "../../components/BranchTreePicker";
import project from "./project"
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

export default {
  components: {
    baseTable,
    basePage,
    BranchTreePicker,
    project
  },
  name:"m_error",
  data() {
    return {
      dateRange:[],
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            end.setTime(end.getTime() + 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            end.setTime(end.getTime() + 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            end.setTime(end.getTime() + 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      rules: {
        pj_classify: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
        site_no: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
        content_text: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
        risk_level: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
        requirement: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
        end_date: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
        responsible: [{
          required: true,
          message: "",
          trigger: "blur"
        },],
      },
      visible: false,
      role_id: sessionStorage.getItem("role_id"),
      role: sessionStorage.getItem("role"),
      form: {},
      dataList: [
        {
          label: "",
          prop: "id",
        },
        {
          label: "",
          prop: "site_name",
        },
        {
          label: "",
          prop: "content_text",

        },
        {
          label: "",
          prop: "risk_level",
        },
        {
          label: "",
          prop: "end_date",
        },
        {
          label: "",
          prop: "requirement",
        },
        {
          label: "",
          prop: "name",
        },
      ],
      items: [
        {
          label: '',
          value: 0
        }, {
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        },
      ],
      projectList: [],
      showData: [],
      disabled: false,
      items2: [
        {
          label: '',
        }, {
          label: '',
        }, {
          label: '',
        },
      ],
      options: [{
        label: '',
        value: '1',
        children: []
      },
        {
          label: '',
          value: '2',
          children: []
        }],
      check: false,
      delList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      userList: [],
      super_manager: this.$loacalData.code_GetEnums(this.$loacalData.listRole, "super_manager").value,
      bankname: "",
      bankid: "",
      status:[],
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      }
      ,
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      }
      ,
    },
    computedStatus() {
      return function (status) {
        return this.items[status]["label"]
      }
    },
  }
  ,
  mounted() {
    this.getMerror();
    this.geBankList();
    this.getSiteSelectList();
    this.getAccountList();
    this.$api.getProjectList({}).then((res) => {
      this.projectList = res.data;
    });
  }
  ,
  methods: {
    edit(status) {
      return !(status === 0);
    },
    // 
    getAccountList() {
      if (this.userList.length != 0) return;
      this.$api.getAccountList({}).then((res) => {
        this.userList = res.data;
      });
    },
    handleChange(itemIdArr) {
      //id
      this.form.site_no = itemIdArr[itemIdArr.length - 1]
    },
    getSiteSelectList() {
      this.$api.getSiteSelectList().then((res) => {
        let showlist = res.data.siteList;
        if (showlist != null) {
          for (let i = 0; i < showlist.length; i++) {
            let defaultProps = {};
            defaultProps.label = showlist[i].site_name;
            defaultProps.value = showlist[i].site_no;
            this.options[1].children.push(defaultProps)
          }
        }
      })
    },
    geBankList() {

      this.$api.getBranch({}).then((res) => {
        let showlist = res.data;
        if (showlist) {
          for (let i = 0; i < showlist.length; i++) {
            let defaultProps = {};
            defaultProps.label = showlist[i].branch_name;
            defaultProps.value = showlist[i].branch_id;
            this.options[0].children.push(defaultProps)
          }
        }
      });
    }

    ,
    getMerror(flag) {
      if (flag) {
        this.pageNum = 1;
      }
      if (this.dateRange !== null && this.dateRange.length > 0 ){
        this.form.start_time = this.dateRange[0];
        this.form.end_time = this.dateRange[1];
      }else {
        delete this.form.start_time;
        delete this.form.end_time;
      }
      //
      this.form.page = this.pageNum - 1;
      this.form.number = this.pageSize;
      if (this.bankid) {
        this.form.site_no = this.bankid;
      }
      if (this.status !== null && this.status.length > 0){
         this.form.status = this.status;
      }
      this.$api.getMerror(this.form).then((res) => {
        this.showData = res.data.resultList;
        this.total = res.data.total;

      });
    }
    ,
    handleClose() {
      this.visible = false;
      this.form = {};
    }
    ,
    addOrEditMerror() {
      this.$refs.form.validate((valid) => {
        if (this.check) {
          this.handleClose()
        } else {
          if (valid) {
            if (this.form.id) {
              delete this.form.rn;
              delete this.form.name;
            }
            this.$api.addMerror(this.form).then(() => {
              this.$message.success("");
              this.handleClose();
              this.getMerror();
            });
          }
        }
      });
    },
    // 
    nodeselectForSerch(node) {
      console.log(node);
      this.bankname = node.branch_name;
      this.bankid = node.branch_id;
    }
    ,
    // 
    addView() {
      this.disabled = false;
      this.visible = true;
      this.form = {};
    }
    ,
    editMerror(obj, flag) {
      if (obj) {
        this.disabled = true;
      }
      if (flag) {
        this.check = flag;
      }
      this.form = JSON.parse(JSON.stringify(obj));
      console.log(this.form)
      this.visible = true;
    }
    ,
    // 
    changeSize(size) {
      this.pageNum = 1;
      this.pageSize = size;
      this.getMerror();
    }
    ,
    // 
    changePage(page) {
      this.pageNum = page;
      this.getMerror();
    }
    ,
    delOne(id) {
      this.delList = [];
      this.delList.push(id);
      this.deleteUser();
    }
    ,
    // 
    chooseData(data) {
      this.delList = data.map((item) => {
        return item.id;
      });
    }
    ,
    // 
    deleteUser() {
      console.log(this.delList);
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true,
      })
          .then(() => {
            this.$api
                .deleteMerror({
                  ids: this.delList,
                })
                .then((res) => {
                  console.log(res);
                  this.pageNum = 1;
                  this.getMerror();
                  this.$message.success("");
                  this.delList = [];
                });
          })
          .catch(() => {
            this.delList = [];
          });
    }
    ,

    // Excel
    uploadFile() {
      let input = document.getElementById("file");
      input.value = "";
      input.click();
    }
    ,
    chooseFile() {
      let that = this;
      let input = document.getElementById("file");
      let file = input.files[0];
      input.value = "";
      this.$api
          .uploadMerror({
            file: file,
          })
          .then((res) => {
            if (res.fail_count == 0) {
              this.$notify.success(`${res.success_count}`);
            } else {
              let arr = "";
              arr = arr.substring(0, arr.length - 2);
              this.$notify.info({
                message: `${res.total_count}${res.success_count}
              ${res.fail_count}`,
              });
            }
            this.getMerror();
          });
    }
    ,
    // 
    downModal() {
      var aTag = document.createElement("a");
      aTag.download = ".xls";
      aTag.href = this.baseURL + "download/error_import.xls";
      aTag.click();
    },
    // 
    exportErrorInfos() {
      //this.loading = true
      delete this.form.page;
      delete this.form.number;
      if (this.dateRange !== null && this.dateRange.length > 0 ){
        this.form.start_time = this.dateRange[0];
        this.form.end_time = this.dateRange[1];
      }else {
        delete this.form.start_time
        delete this.form.end_time
      }
      this.$api.exportErrorInfos(this.form).then((res) => {
        //this.loading = false
        var fileName = res.data.fileName;
        var filePath = res.data.filePath;
        if (null == fileName || null == filePath) {
          this.$message({
            message: "",
            type: "error",
          });
          return false;
        }

        var objSelector = document.createElement("form");
        document.body.appendChild(objSelector);
        objSelector.method = "post";
        objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
        //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
        objSelector.enctype = "multipart/form-data";
        var input01 = document.createElement("input");
        var input02 = document.createElement("input");
        var input03 = document.createElement("input");
        var input04 = document.createElement("input");
        input01.setAttribute("name", "filePath");
        input01.setAttribute("type", "hidden");
        input01.setAttribute("value", filePath);
        input02.setAttribute("name", "fileName");
        input02.setAttribute("type", "hidden");
        input02.setAttribute("value", fileName);
        input03.setAttribute("name", "isDownload");
        input03.setAttribute("type", "hidden");
        input03.setAttribute("value", "1");
        input04.setAttribute("name", "isDelete");
        input04.setAttribute("type", "hidden");
        input04.setAttribute("value", "1");
        objSelector.appendChild(input01);
        objSelector.appendChild(input02);
        objSelector.appendChild(input03);
        objSelector.appendChild(input04);
        objSelector.submit();

        this.$message({
          message: "",
          type: "success",
        });
      });
    },
  }
}
</script>
<style scoped lang="scss">
.showPart{
  text-align: left;
  display:-webkit-box;
  text-overflow:ellipsis;
  white-space: revert;
  overflow:hidden;
  /*3*/
  -webkit-line-clamp: 3;
  -webkit-box-orient:vertical;
}
</style>


