                    return item.role_id === val;
                });
                this.$set(this.form2, "role_name", obj.role_name);
            },
            //data
            initNodeData() {
                this.data = [];
            },
            // 
            appendFirst() {
                this.form2 = {};
                this.visible2 = true;
            },

            //
            checkItem(approval_item){
                try {
                    let node_item = JSON.parse(approval_item);
                    if(Array.isArray(node_item)){
                        this.$message({
                            message: ",",
                            type: "error",
                        });
                        return false;
                    }
                    let keys = Object.keys(node_item);

                    let num1 = 0;
                    let num2 = 0;
                    let num3 = 0;
                    keys.forEach(
                        (key)=>{
                            if(key === ""){
                                num1 = 1;
                            }else if(key === ""){
                                num2 = 1;
                            }else{
                                num3 = 1;
                                return false;
                            }
                        }
                    );
                    if(num3 === 1){
                        this.$message({
                            message: ",/",
                            type: "error",
                        });
                        return false;
                    }
                    if(num1 === 1 && num2 === 1){
                        this.$message({
                            message: ",/",
                            type: "error",
                        });
                        return false;
                    }
                    return this.checkItemDetail(node_item,keys);
                }catch (e) {
                    console.log(e);
                    this.$message({
                        message: ",json",
                        type: "error",
                    });
                }
            },

            checkItemDetail(node_item,keys){
                this.num = "0000";
                this.checkItemDetail2(node_item,keys);
                if(this.num === "0000"){
                    return true;
                }else{
                    return false;
                }
            },




            checkItemDetail2(node_item,keys){

                //
                let num1 = "0";
                let num2 = "0";
                let num3 = "0";
                let num4 = "0";

                for(let i=0;i<keys.length;i++){
                    let key = keys[i];
                    if(key ===""){
                        num1 = "1";
                        if(Array.isArray(node_item.)){
                            for(let j=0;j<node_item..length;j++) {
                                let  = node_item.[j];
                                if(Array.isArray()){
                                    this.$message({
                                        message: ",:"+key+"",
                                        type: "error",
                                    });
                                    this.num = num1+num2+num3+num4;
                                    return false;
                                }
                                if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")){
                                    this.checkItemDetail2(,Object.keys());
                                }
                            }
                        }else{
                            if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")) {
                                this.checkItemDetail2(node_item., Object.keys(node_item.));
                            }
                        }
                    }else if(key ===""){
                        num1 = "1";
                        if(Array.isArray(node_item.)){
                            for(let j=0;j<node_item..length;j++) {
                                let  = node_item.[j];
                                if(Array.isArray()){
                                    this.$message({
                                        message: ",:"+key+"",
                                        type: "error",
                                    });
                                    this.num = num1+num2+num3+num4;
                                    return false;
                                }
                                if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")) {
                                    this.checkItemDetail2(, Object.keys());
                                }
                            }
                        }else{
                            if(num3 ==="0" && num4 ==="0" && (num1==="0" || num2==="0")) {
                                this.checkItemDetail2(node_item., Object.keys(node_item.));
