            createNode() {
                this.$refs.form2.validate((valid) => {
                    if (valid) {
                        if (this.form2.node_code) {
                            // 
                            //
                            if(this.form2.approval_item && !this.checkItem(this.form2.approval_item)){
                                return;
                            }
                            //data
                            let nodedata = {};
                            nodedata.node_code = this.form2.node_code;
                            nodedata.node_id = this.form2.role_id;
                            nodedata.node_name = this.form2.role_name;
                            if(this.form2.approval_item){
                                nodedata.node_item = JSON.parse(this.form2.approval_item);
                            }else{
                                nodedata.node_item = this.form2.approval_item;
                            }
                            //
                            this.updateNodeData(this.data,nodedata);
                            this.form2 = {};
                            this.visible2 = false;

                        } else {
                            // 
                            //
                            if(this.form2.approval_item && !this.checkItem(this.form2.approval_item)){
                                return;
                            }
                            //data
                            let nodedata = {};
                            nodedata.node_code = this.guid();
                            nodedata.node_id = this.form2.role_id;
                            nodedata.node_name = this.form2.role_name;
                            if(this.form2.approval_item){
                                nodedata.node_item = JSON.parse(this.form2.approval_item);
                            }else{
                                nodedata.node_item = this.form2.approval_item;
                            }
                            nodedata.super_code = this.form2.super_code;
                            nodedata.sub_tree = [];
                            this.pushNodeData(this.data,nodedata);
                            this.form2 = {};
                            this.visible2 = false;
                        }
                    }
                })
            },

            pushNodeData(data,nodedata){
                if(!nodedata.super_code){
                    data.push(nodedata);
                }else{
                    data.map((item) => {
                        if(item.node_code == nodedata.super_code){
                            item.sub_tree.push(nodedata);
                        }else{
                            if(item.sub_tree.length>0){
                                this.pushNodeData(item.sub_tree,nodedata);
                            }
                        }
                    });
                }
            },

            updateNodeData(data,nodedata){
                data.map((item) => {
                    if(item.node_code == nodedata.node_code){
                        item.node_id = nodedata.node_id;
                        item.node_name = nodedata.node_name;
                        item.node_item = nodedata.node_item;
                        item.role_id = nodedata.node_id;
                        item.role_name = nodedata.node_name;
                        if(nodedata.node_item){
                            item.approval_item = JSON.stringify(nodedata.node_item,null,4);
                        }else{
                            item.approval_item = nodedata.node_item;
                        }

                    }else{
                        if(item.sub_tree.length>0){
                            this.updateNodeData(item.sub_tree,nodedata);
                        }
                    }
                });
            },

            // 
            append(node, data) {
                this.visible2 = true;
                this.$set(this.form2, "super_code", data.node_code);
            },
            editNode(data) {
                console.log(data);
                this.visible2 = true;
                this.form2 = data;
                this.$set(this.form2, "role_id", data.node_id);
                this.$set(this.form2, "role_name", data.node_name);
                if(data.node_item){
                    this.$set(this.form2, "approval_item", JSON.stringify(data.node_item,null,4));
                }else{
                    this.$set(this.form2, "approval_item", data.node_item);
                }
            },
            removeNode(data) {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning",
                    center: true,
                }).then(() => {
                    //data
                    this.deleteNodeData(this.data,data.node_code);
                }).catch(()=>{});
            },

            deleteNodeData(data,node_code){
                data.map((item) => {
                    if(item.node_code == node_code){
                        if(item.sub_tree.length>0){
                            this.$message({
                                message: "",
                                type: "warning",
                            });
                            return;
                        }
                        let index = data.indexOf(item);
                        if(index > -1){
                            data.splice(index,1);
                        }
                    }else{
                        if(item.sub_tree.length>0){
                            this.deleteNodeData(item.sub_tree,node_code);
                        }
                    }
                });
            },
            //uuid
            guid() {
                function S4() {
                    return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
                };
                return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4());
            },
            // 
            getApprovalItemList() {
                let approval_type = this.$route.query.approval_type;
                this.$api
                    .getApprovalItemList({
                        approval_type: approval_type,
                    })
                    .then((res) => {
                        this.approvalItemList = res.data;
                    });
            },



            // 
            nodeselectForShow(node) {
                this.$set(this.form, "branch_name", node.branch_name);
                this.$set(this.form, "branch_id", node.branch_id);
            },
            // 
            getApprovalRoleList() {
                this.$api.getApprovalRoleList().then((res) => {
                    this.approvalRoleList = res.data;
                });
            },

            // /
            addApprovalProcess() {
                this.$refs.form.validate((valid) => {
                    let approval_type = this.$route.query.approval_type;
                    if (valid) {

                        if(this.data.length < 1){
                            this.$message({
                                message: "",
                                type: "error",
                            });
                            return;
                        }

                        this.form.new_approval_process = JSON.stringify(this.data);
                        //this.$set(this.form, "new_approval_process", JSON.stringify(this.data));
                        if (this.form.id) {
                            this.$api.updateApprovalProcess({
                                approval_type: approval_type,
                                ...this.form,
                            }).then((res) => {
                                this.handleClose();
                                this.getApprovalProcessList();
                            });
                        } else {
                            this.$api.addApprovalProcess({
                                approval_type: approval_type,
                                ...this.form,
                            }).then((res) => {
                                this.handleClose();
                                this.getApprovalProcessList();
                            });
                        }
                    }
                });
            },
            // 
            editApprovalProcess(id) {
                this.$api
                    .getApprovalProcess({
                        id,
                    })
                    .then((res) => {
                        console.log(res);
                        this.visible = true;
                        this.form = res.data.msg[0];
                        this.data = [];
                        if(res.data.msg[0].new_approval_process){
                            this.data = JSON.parse(res.data.msg[0].new_approval_process);
                        }
                    });
            },
            // 
            getApprovalProcessList() {
                let approval_type = this.$route.query.approval_type;
                this.$api
                    .getApprovalProcessList({
                        page: this.pageNum - 1,
                        number: this.pageSize,
                        approval_type: approval_type,
                        ...this.form,
                    })
                    .then((res) => {
                        this.approvalProcessList = res.data.approvalProcessList;
                        this.total = res.data.total;
                        //this.form = {};
                    });
            },
            // 
            changeSize(size) {
                this.pageSize = size;
                this.getApprovalProcessList();
            },
            // 
            changePage(page) {
                this.pageNum = page;
                this.getApprovalProcessList();
            },
            // 
            deleteApprovalProcess(id) {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning",
                    center: true,
                })
                    .then(() => {
                        this.$api
                            .deleteApprovalProcess({
                                id: id,
                            })
                            .then((res) => {
                                console.log(res);
                                this.getApprovalProcessList();
                            });
                    })
                    .catch(() => {
                    });
            },

            handleClose() {
                this.visible = false;
                this.form = {};
            },
        },
        // 
        beforeRouteEnter(to, from, next) {
            next((vm) => {
                if (to.params.searchKey) {
                    vm.form = to.params.searchKey;
                }
                vm.getApprovalProcessList();
            });
        },
    };
