import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'


Vue.use(Vuex)

// 
const state = {
    count: 10,
    bankList: [], // 
    typeList: [], // 
    name:'1',  //,
    loading: false
}

//  mutations
const mutations = {
    getBank(state, data) {
        state.bankList = data
    },
    getBusType(state, data) {
        state.typeList = data
    },
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    },
    changeName(state,data){
     state.name=data
    },
    changeLoading(state, payload) {
        state.loading = payload.loading
    }

}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


